import React from "react";
import style from "./accident.module.css";
import cn from "classnames";
const AccidentView = ({accident, key, deleteAccident}) => {

    let deleteAccidentView = () => {
        deleteAccident(accident.id);
    }

    return <div>
        <p>
            <span className={style.containerAccident} data-bs-toggle="collapse" href={String('#' + "key" + accident.id)}
               role="button" aria-expanded="false" aria-controls="collapseExample">
                {accident.nameAccident + " "}

                <span className={style.rightPos}>
                <button onClick={deleteAccidentView} type="button" className="btn-close btn-close-white" aria-label="Close"></button>
                </span>
                <span className={style.dateTime}>–î–∞—Ç–∞: {" " + accident.date}</span>
            </span>

        </p>

        <div className="collapse" id={String("key" + accident.id)}>
            <div className="card card-body">
                –£–∑–µ–ª: {" " + accident.part}<br/>
                –û–ø–∏—Å–∞–Ω–∏–µ: {" " + accident.description}
            </div>
        </div>
        <br/>
    </div>

}

export default AccidentView;