import React from "react";
import Menu from "../../menu/menuContainer";
import AccidentView from "./accidentView";
import style from "./accident.module.css";
import {NavLink} from "react-router-dom";

const Accident = ({listMachine, accidents, deleteAccident}) => {
    return <div className="container-lg center">
        <Menu/>

            <nav className="navbar navbar-light bg-light">
                <div className="container-fluid">
                    <form className="d-flex">
                        <div className={style.filterAccident}>
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        </div>

                        <div className={style.filterAccident}>
                        <select className="form-select" aria-label="Default select example">
                            <option selected>–≠–∫—Å–∫–∞–≤–∞—Ç–æ—Ä</option>
                            {listMachine.map(m =>
                                <option value={m.id}>{m.number}</option>
                            )}
                        </select>
                                </div>
                            <div className={style.filterAccident}>
                        <select className="form-select" aria-label="Default select example">
                            <option selected>–î–∞—Ç–∞</option>
                            <option value="1">One</option>
                            <option value="2">Two</option>
                            <option value="3">Three</option>
                        </select>
                        </div>
                        <div className={style.filterAccident}>
                        <button className="btn btn-outline-success" type="submit">Search</button>
                            </div>
                        <div className={style.filterAccident}>
                        <NavLink to={'/addaccident'} className="btn btn-primary" type="submit">–î–æ–±–∞–≤–∏—Ç—å –∞–≤–∞—Ä–∏—é</NavLink>
                        </div>
                    </form>
                </div>
                <br/>
                <br/>

            </nav>
            {accidents.map(l =>
                    <AccidentView accident={l} key={l.id} deleteAccident={deleteAccident} />
            )}
    </div>
}

export default Accident;