const ADD_ACCIDENT = 'ADD-ACCIDENT';
const DELETE_ACCIDENT = 'DELETE-ACCIDENT';

export const addAccidentToState = (objAccident) => {
    return {
        type: ADD_ACCIDENT,
        objAccident
    }
}

export const deleteAccident = (idAccident) => {
    return {
        type: DELETE_ACCIDENT,
        idAccident
    }
}

let initialState = {
list: [{id: 0, numberMachine: 58, part: "–î–≤–∏–≥–∞—Ç–µ–ª—å –Ω–∞–ø–æ—Ä–∞", nameAccident: "–ù–µ—Ç –≤–æ–∑–±—É–∂–¥–µ–Ω–∏—è –ø—Ä–∞–≤–æ–≥–æ –¥–≤–∏–≥–∞—Ç–µ–ª—è –Ω–∞–ø–æ—Ä–∞", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 1, numberMachine: 71, part: "–î–≤–∏–≥–∞—Ç–µ–ª—å —Ö–æ–¥–∞ –ª–µ–≤—ã–π", nameAccident: "–ù–µ—Ç —Ö–æ–¥–∞ –æ—Ç –ø–æ–≤–æ—Ä–æ—Ç–∞", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 2, numberMachine: 8, part: "–î–≤–∏–≥–∞—Ç–µ–ª—å —Ö–æ–¥–∞ –ø—Ä–∞–≤—ã–π", nameAccident: "–ù–µ—Ç —Ö–æ–¥–∞ –æ—Ç –Ω–∞–ø–æ—Ä–∞", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 3, numberMachine: 70, part: "–ë–£–°–ú", nameAccident: "–ù–µ —Ä–∞–±–æ—Ç–∞–µ—Ç –¥–∏—Å–ø–ª–µ–π —É –ë–£–°–ú–∞", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" }
]
}
const accidentReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_ACCIDENT:
            return {
                ...state,
                list: [...state.list, action.objAccident]
            }

        case DELETE_ACCIDENT:
            return {
                ...state,
                list: [...state.list.filter(elem => elem.id !== action.idAccident)]
            }

        default: return state;
    }
}

export default accidentReducer;